import React from 'react'
import 'vtex-tachyons'
import PageBlock from '@vtex/styleguide/lib/PageBlock'
import AfilliatesRegister from './components/afilliatesRegister';
import Footer from './components/footer';


function App() {
  return (
    <div className="bg-muted-5 ">
      <Footer />
      <div className="container">
        <PageBlock className="flex-column" variation="full">
          <AfilliatesRegister />
        </PageBlock>
      </div>

    </div>
  );
}

export default App;
