import React from 'react'
import Bruno from '../../assets/Bruno.jpg'
import Jackson from '../../assets/Jackson.jpg'
import Pereira from '../../assets/Pereira.jpg'
import Victor from '../../assets/Victor.jpg'
import Logo from '../../assets/vtex_community.svg'

export default function Footer() {
 return (
  <footer className="bg-base pa1 mb4">
   <ul className="list flex flex-row items-center gap-2">
    <div className="flex items-center mw6">
     <li className="mw5">
      <img
       src={Logo}
       alt="Logo vtex community"
       className="w-80"
      />
     </li>
    </div>

    <div className="flex items-end">
     <li className="flex items-center ">
      <img
       src={Bruno}
       alt="Foto de perfil de Bruno"
       className="br-100 w2-s"
      /><span className="mh3">@bruno.tda</span>
     </li>
     <li className="flex items-center ">
      <img
       src={Jackson}
       alt="Foto de perfil de Jackson"
       className="br-100 w2-s"
      /><span className="mh3">@jacksonadh</span>
     </li>
     <li className="flex items-center ">
      <img
       src={Pereira}
       alt="Foto de perfil de Pereira"
       className="br-100 w2-s"
      /><span className="mh3">@pereirazebral</span>
     </li>
     <li className="flex items-center ">
      <img
       src={Victor}
       alt="Foto de perfil de Victor"
       className="br-100 w2-s"
      /><span className="mh3">@victorcesae</span>
     </li>
    </div>

   </ul>
  </footer>
 )
}