import React from 'react'
import Input from '@vtex/styleguide/lib/Input'
import Button from '@vtex/styleguide/lib/Button'
import Divider from '@vtex/styleguide/lib/Divider'
import Dropdown from '@vtex/styleguide/lib/Dropdown'
import 'vtex-tachyons'
import validateDocument from '../../utils/validateDocument'
import validatePhoneNumber from '../../utils/validatePhone'
import PageBlock from '@vtex/styleguide/lib/PageBlock'

import axios from 'axios';


import { useFormik } from 'formik';
import * as Yup from 'yup';
import ConfirmationPage from '../confirmationPage'


export default function AfilliatesRegister() {
  const [typeDocument, setTypeDocument] = React.useState("CNPJ")
  // const [isModalOpen, setIsModalOpen] = useState(false)
  const [success, setSuccess] = React.useState(false)

  // const [error, setError] = React.useState(false)

  const toggleModalSuccess = () => {
    setSuccess((prevShowModal) => !prevShowModal)
    window.location.reload()
  }

  // const toggleModalError = () => {
  //   setError((prevShowModal) => !prevShowModal)
  // }

  // function handleNew() {
  //   setIsModalOpen(true)
  // }

  // function handleCloseModal() {
  //   setIsModalOpen(false)
  // }
  const formik = useFormik({
    initialValues: {
      fullName: '',
      storeName: '',
      email: '',
      documentType: typeDocument,
      document: '',
      phone: '',
      urlIdentifier: '',
      cep: '',
      number: '',
      street: '',
      neighborhood: '',
      complement: '',
      city: '',
      state: '',
      country: '',
      facebook: '',
      instagram: '',
      whatsapp: '',
      gtmId: '',
    },
    validationSchema: Yup.object({
      fullName: Yup.string().required('Campo obrigat칩rio'),
      storeName: Yup.string().required('Campo obrigat칩rio'),
      email: Yup.string().email('E-mail inv치lido').required('Campo obrigat칩rio'),
      documentType: Yup.string().required('Campo obrigat칩rio').max(4),
      document: Yup.string().required('Campo obrigat칩rio').test('document', 'Documento inv치lido', validateDocument),
      phone: Yup.string().required('Campo obrigat칩rio').min(10, 'minimo de 10 d칤gitos').max(11, 'M치ximo de 11 d칤gitos').test('phone', 'Telefone inv치lido', validatePhoneNumber),
      urlIdentifier: Yup.string().required('Campo obrigat칩rio'),
      cep: Yup.string().required('Campo obrigat칩rio'),
      number: Yup.string().required('Campo obrigat칩rio'),
      street: Yup.string().required('Campo obrigat칩rio'),
      neighborhood: Yup.string().required('Campo obrigat칩rio'),
      city: Yup.string().required('Campo obrigat칩rio'),
      state: Yup.string().required('Campo obrigat칩rio').max(2),
      country: Yup.string().required('Campo obrigat칩rio'),
      whatsapp: Yup.string().required('Campo obrigat칩rio').min(10, 'm칤nimo de 10 d칤gitos').max(11, 'M치ximo de 11 d칤gitos').test('whatsapp', 'Telefone inv치lido', validatePhoneNumber)
    }),
    onSubmit: async (values) => {
      if (values) {
        setSuccess(true)
      }
      // const sendForm = 
      const options = {
        headers: {
          'Content-Type': 'application/json',
          Accept: 'application/json',
        },
        withCredentials: true,
      }

      console.log("values 游", values);
      try {
        const sendInfo = await axios.post('https://yl6mq3v4ga.execute-api.us-east-1.amazonaws.com/Prod/affiliate', values, options)
        console.log("游 ~ file: index.js:70 ~ AfilliatesRegister ~ sendInfo:", sendInfo)
      } catch (error) {
        console.log("游 ~ file: index.js:71 ~ AfilliatesRegister ~ error:", error)
      }

    },
  });

  const options = [
    {
      value: 'cnpj',
      label: 'CNPJ',
    },
    {
      value: 'cpf',
      label: 'CPF',
    },
  ]

  return (
    <PageBlock className="flex-column" variation="full">
      <div>
        <h1>Bem-vindo  nossa plataforma de cadastro de afiliados!</h1>
        <p>Aumente seus ganhos com nosso programa de afiliados. Tenha acesso a recursos exclusivos e uma plataforma intuitiva para rastrear suas vendas e otimizar suas estrat칠gias de marketing.</p>
        <Divider orientation="horizontal" />
        <form onSubmit={formik.handleSubmit}>
          <h3>Informa칞칫es gerais</h3>
          <div className='flex items-center flex-column justify-between w-100'>
            <div className="flex flex-row gap-2 items-start w-100">
              <div className='mb5 w-100'>
                <Input
                  label="Nome completo"
                  size="large"
                  placeholder="Monkey D. Luffy"
                  tabIndex="1"
                  id="fullName"
                  {...formik.getFieldProps('fullName')}
                />
                {formik.touched.fullName && formik.errors.fullName ? (
                  <div className="f7 c-danger mt3">{formik.errors.fullName}</div>
                ) : null}
              </div>
              <div className='mb5 w-100'>
                <Input
                  label="Nome da loja"
                  size="large"
                  placeholder="Thousand Sunny"
                  tabIndex="2"
                  id="storeName"
                  {...formik.getFieldProps('storeName')}
                />
                {formik.touched.storeName && formik.errors.storeName ? (
                  <div className="f7 c-danger mt3">{formik.errors.storeName}</div>
                ) : null}
              </div>
            </div>

            <div className="flex flex-row gap-2 items-start w-100">
              <div className='mb5 w-100'>
                <Input
                  label="E-mail"
                  size="large"
                  placeholder="reidospiratas@mugiwara.com"
                  tabIndex="3"
                  id="email"
                  {...formik.getFieldProps('email')}
                />
                {formik.touched.email && formik.errors.email ? (
                  <div className="f7 c-danger mt3">{formik.errors.email}</div>
                ) : null}
              </div>
              <div className='mb5 w-100'>

                <Dropdown
                  label="Tipo de documento"
                  options={options}
                  value={formik.initialValues.documentType}
                  onChange={(e, value) => (
                    e.preventDefault(),
                    setTypeDocument(value)
                  )}
                  {...formik.getFieldProps('documentType')}

                />

                {formik.touched.documentType && formik.errors.documentType ? (
                  <div className="f7 c-danger mt3">{formik.errors.documentType}</div>
                ) : null}
              </div>
            </div>

            <div className="flex flex-row gap-2 items-start w-100">
              <div className='mb5 w-100'>
                <Input
                  label="Documento"
                  size="large"
                  placeholder="00.000.000/0000-00"
                  tabIndex="5"
                  id="document"
                  {...formik.getFieldProps('document')}
                />
                {formik.touched.document && formik.errors.document ? (
                  <div className="f7 c-danger mt3">{formik.errors.document}</div>
                ) : null}
              </div>

              <div className='mb5 w-100'>
                <Input
                  label="Telefone"
                  size="large"
                  placeholder="(00)0000-0000"
                  tabIndex="6"
                  id="phone"
                  {...formik.getFieldProps('phone')}
                />
                {formik.touched.phone && formik.errors.phone ? (
                  <div className="f7 c-danger mt3">{formik.errors.phone}</div>
                ) : null}
              </div>
            </div>

            <div className="flex flex-row gap-2 items-start w-100">
              <div className='mb5 w-100'>
                <Input
                  label="Identificador da Url"
                  size="large"
                  placeholder="one-piece"
                  tabIndex="7"
                  id="urlIdentifier"
                  {...formik.getFieldProps('urlIdentifier')}
                />
                {formik.touched.urlIdentifier && formik.errors.urlIdentifier ? (
                  <div className="f7 c-danger mt3">{formik.errors.urlIdentifier}</div>
                ) : null}
              </div>
            </div>
          </div>

          <h3>Endere칞o</h3>
          <div className='flex items-center flex-column justify-between w-100'>
            <div className="flex flex-row gap-2 items-start w-100">
              <div className='mb5 w-100'>
                <Input
                  label="CEP"
                  size="large"
                  placeholder="00000-000"
                  tabIndex="8"
                  id="cep"
                  {...formik.getFieldProps('cep')}
                />
                {formik.touched.cep && formik.errors.cep ? (
                  <div className="f7 c-danger mt3">{formik.errors.cep}</div>
                ) : null}
              </div>
              <div className='mb5 w-100'>
                <Input
                  label="N췈"
                  size="large"
                  placeholder="0556"
                  tabIndex="9"
                  id="number"
                  {...formik.getFieldProps('number')}
                />
                {formik.touched.number && formik.errors.number ? (
                  <div className="f7 c-danger mt3">{formik.errors.number}</div>
                ) : null}
              </div>
            </div>

            <div className="flex flex-row gap-2 items-start w-100">
              <div className='mb5 w-100'>
                <Input
                  label="Rua"
                  size="large"
                  placeholder="Use o vivre card"
                  tabIndex="10"
                  id="street"
                  {...formik.getFieldProps('street')}
                />
                {formik.touched.street && formik.errors.street ? (
                  <div className="f7 c-danger mt3">{formik.errors.street}</div>
                ) : null}
              </div>

              <div className='mb5 w-100'>
                <Input
                  label="Bairro"
                  size="large"
                  placeholder="Novo mundo"
                  tabIndex="11"
                  id="neighborhood"
                  {...formik.getFieldProps('neighborhood')}
                />
                {formik.touched.neighborhood && formik.errors.neighborhood ? (
                  <div className="f7 c-danger mt3">{formik.errors.neighborhood}</div>
                ) : null}
              </div>
            </div>

            <div className="flex flex-row gap-2 items-start w-100">
              <div className='mb5 w-100'>
                <Input
                  label="Ponto de referencia / complemento"
                  size="large"
                  placeholder="Vegapunk's lab"
                  tabIndex="12"
                  id="complement"
                  {...formik.getFieldProps('complement')}
                />
              </div>

              <div className='mb5 w-100'>
                <Input
                  label="Cidade"
                  size="large"
                  placeholder="Egg head"
                  tabIndex="13"
                  id="city"
                  {...formik.getFieldProps('city')}
                />
                {formik.touched.city && formik.errors.city ? (
                  <div className="f7 c-danger mt3">{formik.errors.city}</div>
                ) : null}
              </div>
            </div>

            <div className="flex flex-row gap-2 items-start w-100">
              <div className='mb5 w-100'>
                <Input
                  label="Estado"
                  size="large"
                  placeholder="NW"
                  tabIndex="14"
                  id="state"
                  {...formik.getFieldProps('state')}
                />
                {formik.touched.state && formik.errors.state ? (
                  <div className="f7 c-danger mt3">{formik.errors.state}</div>
                ) : null}
              </div>
              <div className='mb5 w-100'>
                <Input
                  label="Pa칤s"
                  size="large"
                  placeholder="Grand Line"
                  tabIndex="15"
                  id="country"
                  {...formik.getFieldProps('country')}
                />
                {formik.touched.country && formik.errors.country ? (
                  <div className="f7 c-danger mt3">{formik.errors.country}</div>
                ) : null}
              </div>
            </div>
          </div>


          <h3>Rede sociais</h3>
          <div className='flex items-center flex-column justify-between w-100'>
            <div className="flex flex-row gap-2 items-start w-100">
              <div className='mb5 w-100'>
                <Input
                  label="Facebook"
                  size="large"
                  placeholder="facebook.com/monkeydluffy"
                  tabIndex="16"
                  id="facebook"
                  {...formik.getFieldProps('facebook')}
                />
              </div>
              <div className='mb5 w-100'>
                <Input
                  label="Instagram"
                  size="large"
                  placeholder="@monkeydluffy"
                  tabIndex="17"
                  id="instagram"
                  {...formik.getFieldProps('instagram')}
                />
              </div>
            </div>

            <div className="flex flex-row gap-2 items-start w-100">
              <div className='mb5 w-100'>
                <Input
                  label="Whatsapp"
                  size="large"
                  placeholder="(00) 9 0556-0556"
                  tabIndex="18"
                  id="whatsapp"
                  {...formik.getFieldProps('whatsapp')}
                />{formik.touched.whatsapp && formik.errors.whatsapp ? (
                  <div className="f7 c-danger mt3">{formik.errors.whatsapp}</div>
                ) : null}
              </div>

              <div className='mb5 w-100'>
                <Input
                  label="ID do GTM"
                  size="large"
                  placeholder="GTM-00000000"
                  tabIndex="19"
                  id="gtmId"
                  {...formik.getFieldProps('gtmId')}
                />
              </div>
            </div>
            <Button type="submit">
              CADASTRAR
            </Button>
          </div>
        </form>
        {success && <ConfirmationPage
          openModal={success}
          size="small"
          showModal={success}
          toggleModal={toggleModalSuccess}
        />}
        {/* {error && <ErrorPage />} */}
      </div>
    </PageBlock>
  )
}