export default function validatePhoneNumber(phoneNumber) {

 if (!phoneNumber) return
 const minLength = 10;
 const maxLength = 11;

 const cleanedNumber = phoneNumber.replace(/\D/g, '');

 if (/^\d+$/.test(cleanedNumber) && cleanedNumber.length >= minLength && cleanedNumber.length <= maxLength) {
  return cleanedNumber
 }

 return 'N칰mero de telefone inv치lido';
}
